@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.know-your-child-wrapper {
    padding-top: 60px;
    padding-bottom: 70px;
    background: linear-gradient(121.33deg, $gradientColor1 6.74%, $gradientColor2 74.57%, $gradientColor3 139.01%);
    @include screen-tablet {
        margin-top: 20px;
        padding-top: 60px;
        padding-bottom: 72px;
        margin-bottom: 50px
    }
    @include screen-mobile {
        margin-top: 20px;
        padding-top: 60px;
        padding-bottom: 72px;
        margin-bottom: 50px
    }

    .content-container {
        .title-wrapper {
            @include font(32px, 700, normal, 48px);
            @include screen-mobile {
                width: 100% !important;
                max-width: 100% !important;
                @include font(24px, 600, normal, 36px);
            }
            .text {
                color: $white;
                @include font(32px, 700, normal, 48px);
                @include screen-mobile {
                    @include font(24px, 600, normal, 36px);
                }
                b {
                    @include font(32px, 700, normal, 32px);
                    @include screen-mobile {
                        @include font(24px, 600, normal, 36px);
                    }
                }
            }
        }
    }
}