@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.educate-your-child-wrapper {
    padding-top: 90px;
    padding-bottom: 180px;
    @include screen-tablet {
        padding-top: 60px;
        padding-bottom: 60px;
    }
    @include screen-mobile {
        padding-top: 60px;
        padding-bottom: 60px;
    }
    .content-container {
        .educate-your-child-conainer {
            margin-right: 2px;
            @include screen-mobile {
                margin-right: 0px;
                display: flex;
                flex-wrap: nowrap;
                overflow-x: auto;
                margin-bottom: 24px;
                &::-webkit-scrollbar {
                    display: none;
                }
            }
        }
    }

}