@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.testimonial-wrapper {
    padding-top: 75px;
    padding-bottom: 75px;
    background: linear-gradient(121.33deg, $gradientColor1 6.74%, $gradientColor2 74.57%, $gradientColor3 139.01%);

    @include screen-tablet {
        margin-top: 60px;
        margin-bottom: 60px;
        padding-top: 60px;
        padding-bottom: 60px;
    }

    @include screen-mobile {
        margin-top: 60px;
        margin-bottom: 60px;
        padding-top: 60px;
        padding-bottom: 60px;
    }

    .testimonial-heading {
        @include font(64px, 700, normal, 84px);
        color: $white;
        margin-bottom: 32px;

        @include screen-mobile {
            @include font(36px, 700, normal, 46px);
        }
    }

    .testimonial-carousel {
        position: relative;

        @include screen-mobile {
            @include gutter(16);
        }

        .carousel {
            .carousel-inner {
                margin-bottom: 40px;
                border-radius: 14px;
                overflow: hidden;

                .carousel-item {
                    border-radius: 14px;
                    overflow: hidden;

                    .testimonial-carousel-item {
                        display: flex;
                        flex-direction: row;
                        flex-wrap: wrap;
                        background-color: $white;
                        border-radius: 14px;
                        overflow: hidden;

                        @include screen-mobile {
                            flex-direction: column;
                        }

                        .image {
                            max-width: 35%;
                            width: 35%;
                            border-radius: 14px 0 0 14px;
                            overflow: hidden;

                            @include screen-mobile {
                                flex-direction: column;
                                max-width: 100%;
                                width: 100%;
                                border-radius: 14px 14px 0 0;
                                margin-bottom: 20px;
                            }
                        }

                        .content-wrapper {
                            max-width: 65%;
                            width: 65%;
                            padding-left: 40px;
                            padding-right: 40px;
                            display: flex;
                            flex-direction: column;
                            flex-wrap: wrap;
                            justify-content: center;

                            @include screen-mobile {
                                flex-direction: column;
                                max-width: 100%;
                                width: 100%;
                                @include gutter(14);
                            }

                            .description {
                                margin-bottom: 40px;
                            }

                            .name {
                                @include font(24px, 600, normal, 36px);
                                color: $black;

                                @include screen-mobile {
                                    margin-bottom: 20px;
                                    @include font(20px, 600, normal, 30px);
                                }
                            }
                        }
                    }
                }
            }

            .carousel-indicators {
                list-style-type: none;
                padding: 0;
                margin: 0;
                display: flex;
                justify-content: center;
                align-content: center;
                position: relative;
                margin-left: 0;
                position: absolute;
                bottom: -40px;

                li {
                    width: 40px;
                    height: 6px;
                    border-radius: 6px;
                    background-color: $white;
                    margin-right: 20px;
                    cursor: pointer;
                    box-sizing: border-box;
                    border: none;
                    opacity: 0.5;
                    cursor: pointer;

                    &:last-child {
                        margin-right: 0;
                    }

                    &.active {
                        opacity: 1;
                    }

                    &:hover {
                        opacity: 1;
                        transition: 0.2s;
                    }
                }
            }

            .carousel-control-prev,
            .carousel-control-next {
                position: absolute;
                top: calc(50% - 15px);
                width: 30px;
                height: 30px;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                opacity: 1;

                @include screen-mobile {
                    height: 16px;
                    width: 16px;
                }

                &::after {
                    content: '';
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    left: 0;
                    top: 0;
                    background-position: center;
                    background-repeat: no-repeat;
                    background-size: contain;
                }

                span {
                    &:first-child {
                        display: none;
                    }
                }

                &.carousel-control-prev {
                    left: -50px;

                    @include screen-tablet {
                        left: -30px;

                    }

                    @include screen-mobile {
                        left: -20px;
                    }

                    &::after {
                        background-image: url(../../assets/images/left-arrow.svg);
                    }
                }

                &.carousel-control-next {
                    right: -50px;

                    @include screen-tablet {
                        right: -30px;

                    }

                    @include screen-mobile {
                        right: -20px;
                    }

                    &::after {
                        background-image: url(../../assets/images/right-arrow.svg);
                    }
                }
            }
        }
    }
}