.card-wrapper {
    .card-wrapper-item {
        display: flex;
        justify-content: flex-start;
        border-radius: 14px;
        color: $lightGrey;
        flex-direction: column;
        align-items: center;
        background: $white;
        box-shadow: 0px 4px 0px $boxShadow1, 0px 2px 7px $boxShadow2;
        height: 100%;
        padding: 36px;
        overflow: hidden;
        position: relative;

        @include screen-mobile {
            padding: 20px 30px;
            margin-bottom: 12px;
            height: auto;
        }

        &.isBorder {
            &::after {
                content: '';
                width: 100%;
                position: absolute;
                bottom: 0;
                background: linear-gradient(135.57deg, $blueGrad1 0.45%, $blueGrad2 50.49%);
                height: 16px;
                border-radius: 0 0 14px 14px;
            }
        }

        .card-header-container {
            display: flex;
            justify-content: center;
            flex-direction: column;
            text-align: center;

            @include screen-mobile {
                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                width: 100%;
                padding-left: 60px;
            }

            .card-title {
                color: $darkBlue;
                @include font(24px, 500, normal, 36px);
                margin-bottom: 5px;

                @include screen-mobile {
                    @include font(20px, 600, normal, 30px);
                }
            }

            .price {
                @include font(20px, 700, normal, 30px);
                color: $darkBlue;

                @include screen-mobile {
                    @include font(16px, 600, normal, 24px);
                }
            }

        }

        .icon {
            margin-top: 20px;
            margin-bottom: 20px;
            min-height: 120px;

            @include screen-mobile {
                position: absolute;
                left: 30px;
                width: 50px;
                top: 20px;
                min-height: auto;
                margin-top: 0px;
                margin-bottom: 0px;
            }
        }

        .description {
            @include font(18px, 400, normal, 34px);
            @include maxLine(2);

            @include screen-mobile {
                @include font(12px, 300, normal, 18px);
                width: 100%;
                padding-left: 60px;
                text-align: left !important;

            }
        }

        .button-wrapper {

            @include screen-mobile {
                margin-top: 10px;
                width: 100%;
                padding-left: 60px;
                @include font(14px, 600, normal, 18px);
            }

        }

    }
}