@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.team-section-wrapper {
    padding-top: 3rem;
    padding-bottom: 3rem;

    @include screen-mobile {
        padding-top: 30px;
    }

    .team-card-wrapper {
        .team-item-wrapper {
            border-radius: 6px 6px 0 0;
            overflow: hidden;
            position: relative;
            margin-bottom: 15px;
            @include screen-mobile {
                max-height: 74px;
                transition: 0.3s ease-in-out;
                margin-bottom: 4px;
            }
            &.isActive {
                @include screen-mobile {
                    max-height: 100%;
                }
            }

            .thumbnail {
                background: linear-gradient(91.14deg, $blueGrad1 0.9%, $blueGrad2 100%);

                span {
                    width: 100%;
                }

                img {
                    width: 100%;
                }
            }

            .team-item-content {
                position: absolute;
                left: 0;
                bottom: 0;
                transform: translateY(calc(100% - 74px));
                background: linear-gradient(91.14deg, $blueGrad1 0.9%, $blueGrad2 100%);
                border-radius: 4px 4px 0 0;
                padding: 12px 20px;

                @include screen-mobile {
                    position: relative;
                    transform: translateY(0);
                }

                .title {
                    margin-bottom: 5px;
                    position: relative;
                    color: $white;
                    @include screen-mobile {
                        margin-bottom: 16px;
                    }

                    .name {
                        @include font(20px, 600, normal, 30px);
                        margin-bottom: 4px;
                        @include maxLine(1);

                        @include screen-mobile {
                            @include font(16px, 600, normal, 24px);
                        }
                    }

                    .designation {
                        @include font(14px, 600, normal, 21px);
                        @include maxLine(1);

                        @include screen-mobile {
                            @include font(12px, 400, normal, 18px);
                        }
                    }

                    .action-button {
                        width: 30px;
                        height: 20px;
                        cursor: pointer;
                        position: absolute;
                        right: 0;
                        top: 50%;
                        transform: rotate(180deg) translateY(-50%);
                        transition: 0.2s ease-in-out;
                        transform-origin: center top;
                        @include screen-mobile {
                            width: 22px;
                            height: 12px;
                        }

                        img {
                            width: 100%;
                        }
                    }
                }
                .thumbnail {
                    @include screen-mobile {
                        margin-bottom: 10px;
                        border-radius: 6px;
                        overflow: hidden;
                    }
                }

                .description {
                    @include font(14px, 400, normal, 21px);
                    color: $white;
                    margin-bottom: 20px;

                    @include screen-mobile {
                        @include font(12px, 400, normal, 18px);
                    }
                }
                .social-media-icons {
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                    .icon {
                        width: 20px;
                        margin-right: 20px;
                        cursor: pointer;
                        img {
                            width: 100%;
                        }
                        &:last-child {
                            margin-right: 0;
                        }
                    }
                }
            }

            &.isShow {
                .thumbnail {
                    img {
                        opacity: 0.6;
                        transition: 0.2s ease-in-out;
                    }
                }

                .team-item-content {
                    transform: translateY(0);
                    transition: 0.2s ease-in-out;

                    .title {
                        .action-button {
                            transform: rotate(180deg) translateY(-50%);
                            transition: 0.2s ease-in-out;
                            transform-origin: center top;
                        }
                    }
                }
            }

            &.isHide {
                .thumbnail {
                    img {
                        opacity: 1;
                        transition: 0.2s ease-in-out;
                    }
                }

                .team-item-content {
                    transform: translateY(calc(100% - 74px));
                    transition: 0.2s ease-in-out;

                    .title {
                        .action-button {
                            transform: rotate(0deg) translateY(-50%);
                            transition: 0.2s ease-in-out;
                            transform-origin: center top;
                        }
                    }
                }
            }
        }

    }

    &.darkBlue {
        .team-item-wrapper {
            .thumbnail {
                background: $darkBlue;

                span {
                    width: 100%;
                }
            }

            .team-item-content {
                background: $darkBlue;

                .title {
                    color: $white;
                }
            }
        }
    }

}