@import '../../../assets/scss/variable';
@import '../../../assets/scss/mixin';

.page-not-found-wrapper {
    background: linear-gradient(121.33deg, $gradientColor1 6.74%, $gradientColor2 74.57%, $gradientColor3 139.01%);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: calc(100vh - 325px);
    @include screen-tablet-portrait {
        height: calc(100vh - 414px);
    }
    @include screen-mobile {
        padding-top: 60px;
        padding-bottom: 60px;
        height: auto;
    }
    .page-not-found-container {
        width: 100%;
        color: $white;
        .heading {
            @include font(140px, 700, normal, 152px);
            @include screen-tablet {
                @include font(70px, 700, normal, 72px);
            }
            @include screen-mobile {
                @include font(52px, 600, normal, 78px);
            }
        }
        .sub-heading {
            @include font(35px, 700, normal, 53px);
            color: $white;
            margin-bottom: 10px;
            @include screen-mobile {
                @include font(32px, 600, normal, 48px);
                letter-spacing: -2%;
            }

        }
        .description {
            @include font(20px, 400, normal, 36px);
            letter-spacing: -2%;
            color: $white;
            margin-bottom: 24px;
            @include screen-mobile {
                @include font(16px, 400, normal, 24px);
            }
        }
        .button-wrapper {
            .btn {
                min-width: 224px;
                @include font(16px, 600, normal, 24px);
            }
        }
        .page-not-found-text {
            height: 100%;
            display: flex;
            justify-content: center;
            @include font(250px, 500, normal, 275px);
            letter-spacing: 10%;
            @include screen-tablet-portrait {
                justify-content: flex-start;
                text-align: left;
            }

            @include screen-mobile {
                justify-content: flex-start;
                @include font(130px, 500, normal, 195px);
            }
        }

    }
}