@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.careers-section-wrapper {
    padding-top: 48px;
    padding-bottom: 122px;
    @include screen-mobile {
        padding-top: 30px;
        padding-bottom: 50px;
    }

    .careers-post-list {
        position: relative;

        &::before {
            content: '';
            width: 2px;
            background-color: $listSeperatorColor;
            position: absolute;
            left: 0;
            top: 0;
            height: 100%;
        }

        @include screen-mobile {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            overflow-x: auto;

            &::-webkit-scrollbar {
                display: none;
            }

            &::before {
                display: none;
            }
        }

        .nav-item {
            width: 100%;
            margin-left: 2px;

            .nav-link {
                padding: 0;
                @include font(20px, 400, normal, 38px);
                color: $black;
                padding-left: 16px;
                position: relative;
                overflow: hidden;
                margin-bottom: 27px;

                @include screen-mobile {
                    overflow: initial;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    padding-bottom: 12px;

                }

                &::before {
                    content: '';
                    width: 4px;
                    border-radius: 0 4px 4px 0;
                    background: linear-gradient(91.14deg, $blueGrad1 0.9%, $blueGrad2 100%);
                    position: absolute;
                    left: 0;
                    top: 0;
                    height: 100%;
                    width: 0px;
                    transition: 0.2s ease-in-out;
                }

                &:hover {
                    &::before {
                        width: 8px;
                        transition: 0.2s ease-in-out;
                    }
                }

                &.active {
                    &::before {
                        width: 8px;
                        transition: 0.2s ease-in-out;
                    }

                    @include screen-mobile {
                        &::before {
                            width: 100%;
                            left: 0;
                        }
                    }
                }

                &.disabled {
                    opacity: 0.5;
                    pointer-events: none;
                }

                @include screen-mobile {
                    @include font(16px, 400, normal, 24px);
                    margin-bottom: 0;
                    padding-left: 2px;
                    padding-right: 2px;

                    &::before {
                        width: 0%;
                        height: 0px;
                        bottom: 0px;
                        left: 50%;
                        top: auto;
                        border-radius: 0 0 6px 6px;
                    }

                    &.active {
                        &::before {
                            height: 4px;
                            width: 100%;
                            left: 0%;
                        }
                    }
                }
            }

            &:last-child {
                .nav-link {
                    margin-bottom: 0px;
                }
            }

            @include screen-mobile {
                padding-left: 14px;
                padding-right: 14px;
                align-self: center;
                white-space: nowrap;
                position: relative;
                margin-left: 0;
                margin-right: 0;
                &::after {
                    content: '';
                    width: 100%;
                    background-color: $listSeperatorColor;
                    position: absolute;
                    left: 0;
                    bottom: 4px;
                    height: 1px;
                }
            }
        }
    }

    .tab-content {
        .tab-pane {
            .heading {
                @include font(32px, 700, normal, 48px);
                color: $darkBlue;
                margin-bottom: 18px;
            }

            .careers-accordion {
                .card {
                    background: $white;
                    border: none;
                    border-radius: 6px;

                    .card-header {
                        padding: 38px 28px;
                        position: relative;
                        background-color: transparent;
                        border: none;
                        @include screen-mobile {
                            padding: 14px;

                        }

                        &::before {
                            content: '';
                            width: calc(100% - 56px);
                            position: absolute;
                            left: 28px;
                            bottom: 0;
                            background-color: $tabHeaderSeperatorColor;
                            height: 1px;
                            @include screen-mobile {
                                width: calc(100% - 28px);
                                left: 14px;
                            }
                        }

                        .heading {
                            margin-bottom: 0;

                            .text {
                                @include font(24px, 600, normal, 36px);
                                color: $black;
                                margin-bottom: 8px;
                                @include screen-mobile {
                                    @include font(16px, 600, normal, 24px);
                                }
                            }

                            .location {
                                @include font(18px, 600, normal, 34px);
                                color: $black;
                                @include screen-mobile {
                                    @include font(12px, 400, normal, 18px);
                                }
                            }
                        }

                        .accordion-trigger {
                            position: absolute;
                            right: 44px;
                            top: 50%;
                            width: 30px;
                            height: 20px;
                            transform: translateY(-50%);
                            cursor: pointer;
                            transition: 0.2s ease-in-out;
                            @include screen-mobile {
                                right: 14px;
                            }

                            &::before {
                                content: '';
                                width: 100%;
                                height: 100%;
                                background-image: url('../../assets/images/accordion-trigger-down.png');
                                background-position: center;
                                background-size: contain;
                                background-repeat: no-repeat;
                                position: absolute;
                                left: 0;
                                top: 0;
                                transition: 0.2s ease-in-out;
                            }

                            img {
                                opacity: 0;
                            }

                            &.isOpen {
                                &::before {
                                    background-image: url('../../assets/images/accordion-trigger-up.png');
                                }

                                transform-origin: center center;
                                transition: 0.2s ease-in-out;
                            }
                            @include screen-mobile {
                                &::before {
                                    background-image: url('../../assets/images/accordion-trigger-mobile.png');
                                    transform: rotate(180deg);
                                    transition: 0.2s ease-in-out;
                                }
                                &.isOpen {
                                    &::before {
                                        background-image: url('../../assets/images/accordion-trigger-mobile.png');
                                        transform: rotate(0deg);
                                        transition: 0.2s ease-in-out;
                                    }
                                }
                            }
                        }
                    }

                    .card-body {
                        padding: 16px 28px;
                        @include screen-mobile {
                            padding: 14px;
                        }

                        .description-title {
                            @include font(14px, 400, normal, 21px);
                            color: $black;
                            margin-bottom: 16px;
                            @include screen-mobile {
                                @include font(14px, 600, normal, 18px);
                                span {
                                    @include font(12px, 400, normal, 18px);
                                }
                            }
                        }

                        .department {
                            @include font(14px, 400, normal, 21px);
                            color: $black;
                            margin-bottom: 16px;
                            @include screen-mobile {
                                @include font(14px, 600, normal, 18px);
                                span {
                                    @include font(12px, 400, normal, 18px);
                                }
                            }
                        }

                        .description-heading {
                            @include font(14px, 400, normal, 21px);
                            color: $black;
                            margin-bottom: 24px;
                            @include screen-mobile {
                                @include font(14px, 600, normal, 18px);
                                span {
                                    @include font(12px, 400, normal, 18px);
                                }
                            }
                        }

                        .description-content {
                            @include font(14px, 400, normal, 21px);
                            color: $tabDescription;
                            @include screen-mobile {
                                @include font(10px, 400, normal, 16px);
                            }
                        }
                    }
                }
            }
        }
    }
}