@import '../../../assets/scss/variable';
@import '../../../assets/scss/mixin';

.chat-wrapper {
    padding-top: 90px;
    padding-bottom: 90px;
    background: linear-gradient(121.33deg, $gradientColor1 6.74%, $gradientColor2 74.57%, $gradientColor3 139.01%);
    @include screen-tablet {
        margin-top: 30px;
        padding-top: 60px;
        padding-bottom: 72px;
    }
    @include screen-mobile {
        margin-top: 30px;
        padding-top: 60px;
        padding-bottom: 72px;
    }

    .content-container {
        .title-wrapper {
            @include screen-mobile {
                margin-bottom: 25px;
            }
            .text {
                color: $white;
            }
        }

        .content-wrapper {
            color: $white;
            @include screen-mobile {
                width: 100% !important;
                max-width: 100% !important;
            }
        }
    }
}