@import '../../../assets/scss/variable';
@import '../../../assets/scss/mixin';

.blog-wrapper {
    margin-bottom: 72px;
    .blog-wrapper-row {
        .blog-wrapper-col {
            .blog-wrapper-wrapper {
                background-color: $white;
                border-radius: 8px;
                margin-bottom: 48px;
                @include screen-mobile {
                    margin-bottom: 20px;
                }
                .thumbnail {
                    border-radius: 8px 8px 0 0;
                    height: 200px;
                    background-position: center;
                    background-size: cover;
                    background-repeat: no-repeat;
                    margin-bottom: 12px;
                }
                .content-wrapper {
                    padding: 0 20px 20px 20px;
                    .publication-date {
                        margin-bottom: 20px;
                        @include font(14px, 400, normal, 21px);
                        color: $darkBlue;
                        @include screen-mobile {
                            @include font(12px, 400, normal, 18px);
                        }
                    }
                    .title {
                        margin-bottom: 8px;
                        @include font(24px, 600, normal, 36px);
                        color: $darkBlue;
                        @include maxLine(3);
                        @include screen-mobile {
                            @include font(16px, 500, normal, 24px);
                            margin-bottom: 10px !important;
                        }
                    }
                    .author-name {
                        @include font(14px, 400, normal, 21px);
                        color: $black;
                        margin-bottom: 32px;
                        @include screen-mobile {
                            @include font(12px, 400, normal, 18px);
                        }
                    }
                    .blog-tags {
                        margin-bottom: 28px;
                        color: $darkBlue;
                        display: flex;
                        justify-content: flex-start;
                        .tags-title {
                            @include font(18px, 400, normal, 34px);
                            margin-right: 8px;
                        }
                        .tags-container {
                            display: flex;
                            overflow-x: auto;
                            justify-content: flex-start;
                            flex-wrap: nowrap;
                            width: 100%;
                            cursor: grab;
                            user-select: none;
                            &::-webkit-scrollbar {
                                display: none;
                            }
                            .tags {
                                border-radius: 100px;
                                background-color: $bodyBackground;
                                height: 34px;
                                width: calc(33.3333% - 8px);
                                flex: 0 0 calc(33.3333% - 8px);
                                @include font(14px, 400, normal, 34px);
                                text-align: center;
                                margin-right: 8px;
                                margin-bottom: 8px;
                                user-select: none;
                                &:last-child {
                                    margin-right: 0;
                                }
                            }
                            &.active {
                                cursor: grabbing;
                            }
                        }
                    }
                    .post-footer {
                        margin-top: 10px;
                        display: flex;
                        justify-content: flex-start;
                        flex-wrap: wrap;
                        .social-media {
                            width: 50%;
                            display: flex;
                            justify-content: flex-start;
                            align-items: center;
                            a {
                                margin-right: 28px;
                                width: 28px;
                                height: 28px;
                                &:last-child {
                                    margin-right: 0;
                                }
                            }
                        }
                        .button-wrapper {
                            width: 50%;
                            display: flex;
                            justify-content: flex-end;
                        }
                    }
                }
            }
        }
    }
}