@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.partners-wrapper {
    padding-top: 60px;
    padding-bottom: 70px;
    @include screen-tablet {
        padding-bottom: 60px;
    }
    @include screen-mobile {
        padding-bottom: 60px;
    }
    .phone-wrapper {
        img {
            @include screen-mobile {
                width: 100%;
            }
        }
    }
}