@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.articles-wrapper {
    padding-top: 75px;
    padding-bottom: 75px;
    @include screen-tablet {
        padding-top: 30px;
        padding-bottom: 30px;
    }
    @include screen-mobile {
        padding-top: 30px;
        padding-bottom: 30px;
    }

    .articles-title {
        @include font(32px, 700, normal, 48px);
        color: $black;
        margin-bottom: 15px;
        @include screen-mobile {
            @include font(24px, 700, normal, 36px);
        }
    }

    .articles-list {
        padding-left: 19px;
        padding-right: 19px;

        @include screen-mobile {
            @include gutter(9);
        }

        ul {
            justify-content: center;

            li {
                margin-bottom: 15px;
                @include screen-mobile {
                    display: flex;
                    justify-content: center;
                }
                
                img {
                    background-color: $white;
                    border-radius: 6px;
                    box-shadow: 0px 4px 0px $boxShadow1, 0px 2px 7px $boxShadow2;
                }
            }

        }
    }
}