.phone-wrapper {
    display: flex;
    justify-content: center;

    @include screen-tablet-portrait {
        height: 100%;
        align-items: center;
    }

    @include screen-mobile {
        justify-content: flex-start;
        @include gutter(9);
    }

    img {
        @include screen-tablet-portrait {
            width: 100%;
        }
        @include screen-mobile {
            width: auto;
        }
    }

    &.our-story-image {
        height: 100%;
        align-items: center;

        @include screen-mobile {
            margin-right: auto;
            margin-bottom: 24px;
        }
    }

    &.educate-your-child {
        @include screen-mobile {
            margin-right: 12px;
        }
        .phone-frame {
            z-index: 1;
            position: relative;
            margin-right: 50px;
            img {
                @include screen-mobile {
                    width: 100%;
                }
            }

            .phone-content-wrapper {
                position: absolute;
                left: 17px;
                top: 44px;
                width: calc(100% - 34px);
                height: calc(100% - 87px);
                border-radius: 28px;
                background-color: $bodyBackground;

                .heading {
                    height: 40px;
                    @include font(16px, 700, normal, 40px);
                    color: $darkBlue;
                    text-align: center;
                    background-color: $white;
                    border-radius: 28px 28px 0 0;
                    &::after {
                        content: '';
                        width: 12px;
                        height: 12px;
                        border: 2px solid $linkBlue;
                        border-top-color: transparent;
                        border-right-color: transparent;
                        position: absolute;
                        left: 15px;
                        top: 12px;
                        transform: rotate(45deg);
                    }
                }

                .clip-body {
                    height: calc(100% - 40px);
                    overflow-y: scroll;
                    position: absolute;
                    left: 0;
                    top: 40px;
                    border-radius: 0 0 28px 28px;
                    scroll-behavior: smooth;

                    /* width */
                    &::-webkit-scrollbar {
                        width: 5px !important;
                    }

                    /* Track */
                    &::-webkit-scrollbar-track {
                        box-shadow: none;
                    }

                    /* Handle */
                    &::-webkit-scrollbar-thumb {
                        background: $lightGrey;
                        border-radius: 6px;
                    }

                    .clip-wrapper {
                        margin-top: 15px;

                        .clip-item {
                            margin-bottom: 20px;
                            margin-left: 6px;
                            margin-right: 6px;
                            background: $white;
                            border-radius: 6px;
                            overflow: hidden;
                            padding: 10px;
                            box-shadow: 0px 4px 0px 0px lighten($lightGrey, 40%);

                            .image {
                                margin-bottom: 5px;
                            }

                            .title {
                                @include font(14px, 500, normal, 14px);
                                color: $darkBlue;
                                margin-bottom: 3px;
                            }

                            .time {
                                @include font(10px, 400, normal, 12px);
                                color: $darkBlue;
                            }

                            &.isActive {
                                background: linear-gradient(135.57deg, $blueGrad1 0.45%, $blueGrad2 50.49%);
                                .title {
                                    color: $white;
                                }
                                .time {
                                    color: $white;
                                }
                            }
                        }
                    }

                }

                .clip-video-wrapper {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    opacity: 0;
                    visibility: hidden;
                    transform: translateX(-50%) translateY(-50%);
                    z-index: 3;
                    padding: 10px;
                    border-radius: 6px;
                    overflow: hidden;
                    background-color: $white;
                    @include screen-mobile {
                        left: 0;
                        width: 100%;
                        transform: scale(1.2) translateX(0%) translateY(-50%);
                    }

                    &.isShow {
                        opacity: 1;
                        visibility: visible;
                        transition: 0.2s ease-in-out;
                    }
                }
            }
        }

        .phone-character {
            max-width: 120px;
            position: absolute;
            right: 0;
            bottom: 0;
            z-index: 2;
        }
    }
}