@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.children-key-features-wrapper {
    padding-top: 2rem;
    padding-bottom: 2rem;
    .content-container {
        .title-wrapper {
            // @include screen-mobile {
            //     @include font(36px, 500, normal, 46px);
            //     margin-bottom: 8px;
            // }
            // b {
            //     @include screen-mobile {
            //         @include font(36px, 700, normal, 46px);
            //     }
            // }
            .icon {
                max-width: 84px;
            }
        }
    }
    .children-key-features-container {
        @include screen-mobile {
            @include gutter(9);
            display: flex;
            flex-wrap: nowrap;
            overflow-x: auto;
            &::-webkit-scrollbar {
                display: none;
            }
        }
        .children-key-features-row {
            margin-bottom: 30px;
            align-items: center;
            @include screen-mobile {
                margin-bottom: 0;
                flex: 0 0 100%;
                margin-left: 0;
                margin-right: 0;
                align-self: flex-start;
            }
            &:first-child {
                @include screen-mobile {
                    flex: 0 0 90%;
                }
                .children-key-features-col {
                    .phone-wrapper {
                        img {
                            @include screen-mobile {
                                width: 75% !important;
                            }
                        }
                    }
                }
            }
            &:last-child {
                @include screen-mobile {
                    flex: 0 0 90%;
                    margin-bottom: 0;
                }
                .children-key-features-col {
                    .phone-wrapper {
                        img {
                            @include screen-mobile {
                                width: 75% !important;
                            }
                        }
                    }
                    .children-key-feature-container {
                        @include screen-mobile {
                            margin-right: 0;   
                        }
                    }
                }
            }
            .children-key-features-col {
                @include screen-mobile {
                    padding-right: 0;
                    padding-left: 0;
                    
                }
                .phone-wrapper {
                    @include screen-tablet {
                        span {
                            text-align: center;
                        }
                    }
                    img {
                        width: 60%;
                        margin-left: auto;
                        margin-right: auto;
                        @include screen-mobile {
                            width: 67%;
                        }
                    }
                }
                .children-key-feature-container {
                    height: 100%;
                    @include screen-mobile {
                        height: auto;
                        background-color: $white;
                        border-radius: 6px;
                        box-shadow: 0px 4px 0px $boxShadow1, 0px 2px 7px $boxShadow2;
                        padding: 14px;
                        margin-right: 12px;
                    }
                    .heading {
                        display: flex;
                        justify-content: flex-start;
                        flex-direction: column;
                        @include screen-mobile {
                            flex-direction: row;
                            justify-content: flex-start;
                            align-items: center;
                            margin-bottom: 10px;
                        }
                        .icon {
                            margin-bottom: 10px;
                            max-width: 52px;
                            @include screen-mobile {
                                max-width: 42px;
                                margin-right: 12px;
                                margin-bottom: 0px
                            }
                            img {
                                width: 100%;
                            }
                        }
                        .title {
                            @include font(28px, 500, normal, 42px);
                            color: $darkBlue;
                            @include screen-mobile {
                                @include font(16px, 600, normal, 24px);
                                color: $black;
                            }
                        }
                    }
                    .description {
                        @include font(20px, 400, normal, 36px);
                        @include screen-mobile {
                            @include font(16px, 400, normal, 24px);
                            @include maxLine(4)
                        }
                    }
                }
            }
            .children-key-features-heading {
                width: 100%;
                padding-left: 0px;
                margin-bottom: 20px;
                height: 45px;
                .content-container {
                    padding-left: 0;
                    padding-right: 0;
                    .title-wrapper {
                        margin-bottom: 0 !important;
                
                        @include screen-mobile {
                            @include font(24px, 700, normal, 36px);
                            margin-bottom: 8px;
                        }
                
                        b {
                            @include screen-mobile {
                                @include font(24px, 700, normal, 36px);
                            }
                        }
                        .icon {
                            width: 60px; 
                            img {
                                width: 100%;
                            }
                        }
                    }
                }
                .col-12 {
                    padding-right: 0;
                }
            }
            .placeholder-label {
                height: 45px;
                width: 100%;
                margin-bottom: 20px;
            }
        }
    }
}