@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.key-features-wrapper {
    padding-top: 130px;
    padding-bottom: 90px;
    @include screen-tablet {
        padding-top: 60px;
        padding-bottom: 60px;
    }
    @include screen-mobile {
        padding-top: 60px;
        padding-bottom: 60px;
    }

    .content-container {
        padding-left: 10px;
        padding-right: 10px;

        
        .key-feature-conainer {
            margin-bottom: 30px;
            display: flex;
            justify-content: flex-start;
            flex-wrap: wrap;
            margin-left: -10px;
            margin-right: -10px;
            @include screen-mobile {
                flex-wrap: nowrap;
                overflow-x: auto;
                margin-left: 0;
                margin-right: 0;
                margin-bottom: 24px;
                &::-webkit-scrollbar {
                    display: none;
                }
            }
        }

        .button-wrapper {
            margin-left: 20px;
            @include screen-mobile {
                margin-left: 0;
            }
        }
    }

    .phone-wrapper {
        &.key-features {
            img {
                @include screen-mobile {
                    max-width: 256px;
                    margin: auto;
                }
                &.image-out {
                    opacity: 0;
                    visibility: hidden;
                    transition: 0.5s;
                }
                &.image-in {
                    opacity: 1;
                    visibility: visible;
                    transition: 0.5s;
                }
            }
        }
    }
}