@import "../../assets/scss/variable";
@import "../../assets/scss/mixin";

.faq-wrapper {
    padding-top: 72px;
    padding-bottom: 90px;
    @include screen-tablet {
        padding-top: 30px;
        padding-bottom: 60px;
    }
    @include screen-mobile {
        padding-top: 30px;
        padding-bottom: 60px;
    }
    .faq-accordion {
        padding-top: 24px;

        @include screen-mobile {
            padding-top: 0;
        }

        .card {
            margin-bottom: 6px;
            border-radius: 6px;
            border: none;
            background-color: $white;

            .card-header {
                padding: 20px;
                background-color: $white;
                border: none;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                width: 100%;

                @include screen-mobile {
                    align-items: flex-start;
                }

                .faq-title {
                    @include font(24px, 500, normal, 36px);
                    @include maxLine(1);
                    color: $black;
                    width: calc(100% - 40px);

                    @include screen-mobile {
                        @include font(16px, 500, normal, 24px);
                        overflow: initial;
                        text-overflow: initial;
                        -webkit-line-clamp: initial;
                        height: 100%;
                        align-self: center;
                    }
                }

                .faq-button {
                    width: 30px;
                    height: 30px;
                    cursor: pointer;
                    position: relative;
                    transform-origin: center center;
                    transition: 0.2s ease-in-out;

                    &::before {
                        content: '';
                        width: 100%;
                        height: 100%;
                        background-image: url('../../assets/images/accordion-trigger-down.png');
                        background-position: center;
                        background-size: contain;
                        background-repeat: no-repeat;
                        position: absolute;
                        left: 0;
                        top: 0;
                    }

                    img {
                        width: 100%;
                        opacity: 0;

                        @include screen-mobile {
                            opacity: 1;
                        }
                    }

                    &.isOpen {
                        &::before {
                            background-image: url('../../assets/images/accordion-trigger-up.png');
                        }

                        transform-origin: center center;
                        transition: 0.2s ease-in-out;
                    }
                }
            }

            .card-body {
                @include font(18px, 400, normal, 34px);

                @include screen-mobile {
                    @include font(12px, 600, normal, 18px);
                    padding-top: 2px;
                }
            }
        }
    }
}