@import '../../../assets/scss/variable';
@import '../../../assets/scss/mixin';

.blog-description-wrapper {
    .banner-wrapper {
        height: 106px;
        background: linear-gradient(121.33deg, $gradientColor1 6.74%, $gradientColor2 74.57%, $gradientColor3 139.01%);
        border-radius: 0 0 30px 30px;
        margin-bottom: 55px;
        @include screen-mobile {
            height: 67px;
        }
    }

    .tittle-wrapper {
        position: relative;
        padding-left: 62px;
        margin-bottom: 60px;
        @include screen-mobile {
            padding-left: 40px;
        }

        .back-button {
            position: absolute;
            left: 0;
            top: 34px;
            width: 25px;
            height: 25px;
            border: 1px solid $black;
            border-top-color: transparent;
            border-right-color: transparent;
            transform: rotate(45deg);
            @include screen-mobile {
                top: 12px;
                width: 18px;
                height: 18px;
            }
        }

        .title {
            @include font(64px, 700, normal, 84px);
            color: $darkBlue;
            margin-bottom: 28px;
            @include screen-mobile {
                @include font(36px, 700, normal, 46px);
            }
        }

        .blog-details {
            display: flex;
            justify-content: flex-start;
            flex-direction: row;
            @include screen-mobile {
                position: relative;
                width: calc(100% + 40px);
                left: -40px;
            }
            .author-name {
                margin-right: 60px;
                @include screen-mobile {
                    width: 50%;
                    margin-right: 0;
                }
            }
            .publish-date {
                @include screen-mobile {
                    color: $darkBlue;
                    width: 50%;
                    margin-right: 0;
                }
            }
        }
    }
    .blog-thumbnail {
        height: 500px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        border-radius: 8px;
        margin-bottom: 60px;
        @include screen-mobile {
            height: 200px;
            margin-bottom: 40px;
        }
    }
    .blog-content {
        margin-bottom: 72px;
        width: 72%;
        margin-left: auto;
        margin-right: auto;
        @include screen-mobile {
            width: 100%;
        }
        h1, h2, h3, h4, h5, h6 {
            color: $darkBlue;
            margin-bottom: 20px
        }
        h1 {
            @include font(64px, 700, normal, 84px);
            @include screen-mobile {
                @include font(32px, 700, normal, 48px);
            }
        }
        h2 {
            @include font(32px, 700, normal, 48px);
            @include screen-mobile {
                @include font(24px, 700, normal, 36px);
            }
        }
        h3 {
            @include font(28px, 600, normal, 84px);
            @include screen-mobile {
                @include font(24px, 600, normal, 36px);
            }
        }
        h4 {
            @include font(24px, 600, normal, 36px);
            @include screen-mobile {
                @include font(16px, 500, normal, 24px);
            }
        }
        h5 {
            @include font(20px, 400, normal, 24px);
            @include screen-mobile {
                @include font(14px, 400, normal, 24px);
            }
        }
        h6 {
            @include font(14px, 400, normal, 21px);
            @include screen-mobile {
                @include font(12px, 400, normal, 20px);
            }
        }
        blockquote {
            border-left: 10px solid $lightGrey;
            padding: 10px 0px 10px 20px;
            margin-bottom: 32px;
            p {
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
        p {
            @include font(14px, 400, normal, 21px);
            color: $lightGrey;
            margin-bottom: 32px;
            @include screen-mobile {
                @include font(16px, 400, normal, 24px);
            }
        }
        ul {
            list-style-type: disc;
            padding-left: 20px;
            li {
                p {
                    &:last-child {
                        margin-bottom: 0px;
                    }
                }
            }
        }
        ol {
            padding-left: 20px;
            li {
                position: relative;
                &::marker {
                    position: absolute;
                    left: 0;
                    top: 0;
                    line-height: 1;
                    text-align: left;
                }
                p {
                    &:last-child {
                        margin-bottom: 0px;
                    }
                }
            }
        }
        hr {
            border-top-color: $lightGrey;
        }
    }
    .related-blogs-wrapper {
        .related-blogs-title {
            @include font(64px, 700, normal, 84px);
            color: $darkBlue;
            margin-bottom: 32px;
        }
        .related-blogs-container {
            .blog-wrapper-row {
                flex-wrap: nowrap;
                overflow-x: auto;
                &::-webkit-scrollbar {
                    display: none;
                }
            }
        }
    }
}