@for $w from 1 to 101 {
    .wid-#{$w} {
        width: $w * 1% !important;
        max-width: $w * 1% !important;
    }
    .wid-sm-#{$w} {
        @include screen-mobile {
            width: $w * 1% !important;
            max-width: $w * 1% !important;
        }
    }
    .wid-md-#{$w} {
        @include screen-tablet {
            width: $w * 1% !important;
            max-width: $w * 1% !important;
        }
    }
    .wid-lg-#{$w} {
        @include screen-laptops {
            width: $w * 1% !important;
            max-width: $w * 1% !important;
        }
    }
}

.hidden {
    display: none !important;
}

.hidden-sm {
    @include screen-mobile {
        display: none !important;
    }
}

.hidden-md {
    @include screen-tablet {
        display: none !important;
    }
}

.hidden-lg {
    @include screen-laptops {
        display: none !important;
    }
}