@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.career-wrapper {
    padding-top: 90px;
    padding-bottom: 90px;
    @include screen-tablet {
        padding-top: 60px;
        padding-bottom: 60px;
    }
    @include screen-mobile {
        padding-top: 60px;
        padding-bottom: 60px;
    }
    .careers-posts-scroll {
        margin-bottom: 20px;
        .careers-posts-item {
            background-color: $white;
            border-radius: 6px;
            padding: 20px;

            @include screen-mobile {
                margin-bottom: 15px;
            }

            .name {
                color: $darkBlue;
                @include font(24px, 600, normal, 36px);
                margin-bottom: 5px;
                @include screen-mobile {
                    @include font(20px, 600, normal, 30px);
                }
            }
            .location {
                color: $darkBlue;
                @include font(18px, 400, normal, 34px);
                margin-bottom: 15px;
                @include screen-mobile {
                    @include font(14px, 600, normal, 20px);
                }
            }
        }
    }
}