@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.in-focus-wrapper {
    padding-top: 3rem;
    padding-bottom: 3rem;
    background: linear-gradient(121.33deg, $gradientColor1 6.74%, $gradientColor2 74.57%, $gradientColor3 139.01%);
    .content-container {
        height: auto;
        .title-wrapper {
            color: $white;
        }
    }
    .in-focus-container {
        padding-left: 50px;
        padding-right: 50px;
        @include screen-mobile {
            padding-left: 20px;
            padding-right: 20px;
        }
        .in-focus-row {
            margin-left: -6px;
            margin-right: -6px;
            .in-focus-col {
                padding-left: 6px;
                padding-right: 6px;
                .in-focus-card {
                    background: $white;
                    border-radius: 6px;
                    overflow: hidden;
                    .thumbnail {
                        margin-bottom: 10px;
                    }
                    .in-focus-body {
                        padding: 15px 20px;
                        .source-thumbnail {
                            margin-bottom: 10px;
                            img {
                                max-width: 180px;
                            }
                        }
                        .title {
                            @include font(24px, 500, normal, 36px);
                            @include maxLine(3);
                            color: $black;
                            @include screen-tablet {
                                min-height: 108px;
                            }
                        }
                        .description {
                            @include font(18px, 500, normal, 34px);
                            @include maxLine(3);
                            color: $descriptionColor;
                            margin-bottom: 15px;
                        }
                    }
                }
            }
        }
    }
}