@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.personel-card {
    padding-top: 100px;
    padding-bottom: 120px;
    @include screen-tablet {
        padding-top: 50px;
        padding-bottom: 60px;
    }
    @include screen-mobile {
        padding-top: 50px;
        padding-bottom: 60px;
    }
    .phone-wrapper {
        @include screen-mobile {
            margin-bottom: 25px;
            img {
                width: 100%;
            }
        }
    }
    .content-container {
        .title-wrapper {
            &.isIcon {
                &.inline {
                    .text {
                       .right-inline {
                           img {
                               max-width: 80px;
                           }
                       } 
                    }
                }
            }
        }
    }
}
