@import '../../../assets/scss/variable';
@import '../../../assets/scss/mixin';

.social-wrapper {
    padding-top: 90px;
    padding-bottom: 144px;
    @include screen-tablet {
        padding-top: 60px;
        padding-bottom: 60px;
    }
    @include screen-mobile {
        padding-top: 60px;
        padding-bottom: 60px;
    }
    .content-container {
        .content-wrapper {
            @include screen-mobile {
                margin-bottom: 5px !important;
            }
        }
    }
    .posts-thumbnail-wrapper {
        @include screen-mobile {
            margin-bottom: 0 !important;
        }
        .posts-thumbnail-item-row {
            @include screen-mobile {
                flex-wrap: nowrap;
                overflow-x: auto;
                margin-left: -6px;
                margin-right: -6px;
                &::-webkit-scrollbar {
                    display: none;
                }
            }
            .posts-thumbnail-item-col {
                @include screen-mobile {
                    flex: 0 0 70%;
                    padding-left: 6px;
                    padding-right: 6px;
                }
                .posts-thumbnail-item {
                    @include screen-mobile {
                        margin-bottom: 15px;
                    }
                    img {
                        width: 100%;
                    }
                }
            }
        }
    }
}