@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.why-muvin-wrapper {
    padding-top: 190px;;
    padding-bottom: 130px;
    @include screen-tablet {
        padding-top: 120px;
        padding-bottom: 60px;
    }
    @include screen-mobile {
        padding-top: 120px;
        padding-bottom: 60px;
    }
    .phone-wrapper {
        img {
            @include screen-mobile {
                width: 100%;
            }
        }
    }
}