@import'../../assets/scss/variable';
@import'../../assets/scss/mixin';

.metric-wrapper {
    padding-top: 250px;
    padding-bottom: 90px;
    display: flex;
    align-items: center;
    @include screen-tablet {
        min-height: auto;
        padding-top: 120px;
        padding-bottom: 60px;
    }
    @include screen-mobile {
        min-height: auto;
        padding-top: 60px;
        padding-bottom: 60px;
    }

    .metric-item {
        @include screen-mobile {
            text-align: center;
            margin-bottom: 20px;
        }
        .icon {
            margin-bottom: 0;
        }

        .counter {
            @include font(64px, 600, normal, 84px);
            font-size: calc(min(max(36px, 5vw), 60px));
            display: flex;
            justify-content: flex-start;
            align-items: center;
            color: $darkBlue;
            @include screen-mobile {
                justify-content: center;
                @include font(64px, 700, normal, 84px);
                font-size: calc(min(max(36px, 5vw), 60px));
            }
        }

        .title {
            @include font(28px, 500, normal, 42px);
            color: $darkBlue;
            @include screen-mobile {
                justify-content: center;
                @include font(28px, 500, normal, 42px);
            }
        }
    }
}