@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.feature-key-item {
    width: 50%;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 5px;
    margin-bottom: 5px;
    @include screen-tablet-portrait {
        width: 100%;
        flex: 0 0 100%;
        padding-left: 0px;
        padding-right: 0px;
        margin-right: 12px;
    }
    @include screen-mobile {
        width: 80%;
        flex: 0 0 80%;
        padding-left: 0px;
        padding-right: 0px;
        margin-right: 12px;
    }
    .feature-key-container {
        padding: 20px;
        background-color: transparent;
        transition: 0.2s ease-in-out;
        position: relative;
        cursor: default;

        @include screen-tablet-portrait {
            padding: 5px 15px;
        }

        @include screen-mobile {
            padding: 14px;
            background-color: $white;
            border-radius: 4px;
            height: 100%;
            box-shadow: 0px 4px 0px $boxShadowColor1, 0px 2px 7px $boxShadowColor2;
            &::after, &::before {
                display: none;
            } 
        }

        &::after {
            content: '';
            width: 10px;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            background-color: transparent;
            transition: 0.2s ease-in-out;
            z-index: 2;
        }
        &::before {
            content: '';
            width: 0;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            z-index: 1;
            background-color: transparent;
            transition: 0.2s ease-in-out;
        }
    
        &:hover, &.isActive {
            &::after {
                background: linear-gradient(91.14deg, $blueGrad1 0.9%, $blueGrad2 100%);
                transition: 0.3s ease-in-out;
            }
            &::before {
                width: 100%;
                background: $white;
                transition: 0.3s ease-in-out;
            }
        }
        

        .feature-title {
            @include font(24px, 500, normal, 36px);
            margin-bottom: 2px;
            color: $darkBlue;
            position: relative;
            z-index: 3;
            @include screen-mobile {
                @include font(16px, 500, normal, 24px);
            }
        }

        .feature-description {
            color: $lightGrey;
            position: relative;
            z-index: 3;
            @include screen-mobile {
                @include font(16px, 400, normal, 24px);
            }
        }

    }
}