@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.our-mission-wrapper {
    padding-bottom: 90px;
    padding-top: 90px;
    @include screen-tablet {
        padding-top: 50px;
        padding-bottom: 50px;
    }
    @include screen-mobile {
        padding-top: 50px;
        padding-bottom: 50px;
    }
    .phone-wrapper {
        @include screen-tablet-portrait {
            align-items: flex-start !important;
            span {
                position: sticky;
                top: 120px;
            }
        }
        @include screen-mobile {
            margin-bottom: 6px;
        }
    }
    .content-container {
        .title-wrapper {
            @include screen-mobile {
                margin-bottom: 12px;
            }
            .text {
                @include screen-mobile {
                    @include font(36px, 700, normal, 36px);
                    b {
                        @include font(36px, 700, normal, 36px);
                    }
                }
            }
            .icon {
                @include screen-mobile {
                    max-width: 90px;
                }
                img {
                    @include screen-mobile {
                        width: 100%;
                    }
                }
            }
        }
        .content-wrapper {
            &.isList {
                @include screen-mobile {
                    margin-bottom: 0;
                }
                .isListTitleDescription {
                    @include screen-mobile {
                        margin-bottom: 16px;
                        &:last-child {
                            margin-bottom: 0;
                        }
                    }
                    .title {
                        @include screen-mobile {
                            @include font(16px, 600, normal, 24px);
                        }
                    }
                    .description {
                        @include screen-mobile {
                            @include font(16px, 400, normal, 24px);
                        }
                    }
                }
            }
        }
    }
}