@import '../../../assets/scss/variable';
@import '../../../assets/scss/mixin';

.partners-banner {
    .carousel-wrapper {
        .carousel-body {
            .carousel-body-left {
                .text-wrapper {
                    b {
                        font-size: calc(min(max(52px, 10.6vw), 70px));
                    }
                }
            }
        }
    }

}

.why-parner-with-us-section {
    padding-top: 3rem;
    padding-bottom: 3rem;

    .phone-wrapper {
        height: 100%;
        display: flex;
        align-items: center;
    }

    .content-container {
        .title-wrapper {
            .text {
                font-size: calc(min(max(32px, 10vw), 62px));

                b {
                    font-size: calc(min(max(32px, 10vw), 62px));
                }
            }
        }
    }
}

.partnership-types-section {
    padding-top: 100px;
    padding-bottom: 130px;
    @include screen-mobile {
        padding-top: 60px;
        padding-bottom: 60px;
    }
    .content-container {
        .title-wrapper {
            margin-bottom: 54px;
        }
    }
    .partnership-types-image {
        img {
            width: 100%;
        }
    }
}